#root{
  margin:20px 32.5px 0;
}
body{
  margin: 0;
}


@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;900&family=Kumbh+Sans:wght@500&family=Roboto:wght@500&display=swap');

/* h1{
  font-family:'Inter', sans-serif;
